.ContainerLinks2{
   display: none;
}
.ContainerLinks2-active{
    grid-column: 1/3;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    grid-gap: 20px;
    gap: 20px;
    width: 100%;
    height: 0px;
    background-color: #a06ab4;
    -webkit-animation: show 0.5s linear forwards;
            animation: show 0.5s linear forwards;
    padding: 10px 5px;
    overflow: hidden;
}
@-webkit-keyframes show {
    0%{
        height: 0px;
    }
    100%{
        height: 300px;
    }
}
@keyframes show {
    0%{
        height: 0px;
    }
    100%{
        height: 300px;
    }
}
.NabvarLink2{
    font-size: 20px;
    color: white;
    text-decoration: none;
    text-overflow: none;
    -webkit-animation: show3  linear forwards;
            animation: show3  linear forwards;
    -webkit-animation-delay: 0.3s;
            animation-delay: 0.3s;
}
.NabvarLink2:visited{
    font-size: 25px;
    color: white;
    text-decoration: none;
    text-overflow: none;
    -webkit-animation: show3  linear forwards;
            animation: show3  linear forwards;
    -webkit-animation-delay: 0.3s;
            animation-delay: 0.3s;
}
.ContainerLinks2-desactive{
    grid-column: 1/3;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    grid-gap: 20px;
    gap: 20px;
    width: 100%;
    height: 300px;
    background-color: #a06ab4;
    -webkit-animation: show4 0.5s linear forwards;
            animation: show4 0.5s linear forwards;
    padding: 10px 5px;
}
@-webkit-keyframes show4 {
    0%{
        height: 300px;
    }
    99%{
        height: 0px;
    }
    100%{
        padding: 0px 5px;
        height: 0px;
    }
}
@keyframes show4 {
    0%{
        height: 300px;
    }
    99%{
        height: 0px;
    }
    100%{
        padding: 0px 5px;
        height: 0px;
    }
}
