.Loading1{
    background-color: #a06ab4 ;
    border: solid transparent;
    border-radius: 50%;
    width: 4vw;
    height: 4vw;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 100%;
    

    -webkit-animation: opaccity 1s infinite;
    

            animation: opaccity 1s infinite;    /*Name___Duration___Timing___Delay*/
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-delay: 1s;
            animation-delay: 1s; 
    margin-left: 10px;
}

@-webkit-keyframes opaccity {
    from{
        opacity: 100%;
    }
    to{
        opacity: 0%;
    }
}

@keyframes opaccity {
    from{
        opacity: 100%;
    }
    to{
        opacity: 0%;
    }
}


