.FondoBlanco{
  position: absolute; /*PROPIEDAD IMPORTANTE PARA LA ANIMACION SIT_AND_ENJOY */
  background-color: white;
  z-index: 999;
  width: 100%;
  height: 100%;
  -webkit-animation: sit_and_enjoy 0.5s  forwards;
          animation: sit_and_enjoy 0.5s  forwards;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}
@-webkit-keyframes sit_and_enjoy {
  0%{
    opacity: 100%;
  }
  100%{
    opacity: 0%;
    z-index: -1; /*ES UN DETALLE IMPORTANTE PORQUE SINO EL TELON NO DEJA INTERACTUAR CON LA PAGINA*/
  }
}
@keyframes sit_and_enjoy {
  0%{
    opacity: 100%;
  }
  100%{
    opacity: 0%;
    z-index: -1; /*ES UN DETALLE IMPORTANTE PORQUE SINO EL TELON NO DEJA INTERACTUAR CON LA PAGINA*/
  }
}
