.ContainerPublicaciones{
    height: auto;
    width: 100%;
    padding: 0% 0%;
    background-color: #d4a9e4;
    border-top: solid white;
}
.ContainerAux{
    padding: 4% 1%;
    border-top: solid white;
}
.ContainerPublicacionesTitulo{
    width: 100%;
    height: auto;
    text-align: center;
}
.PublicacionesTitulo{
    color: white;
    font-size: 5vw;
    font-family: "dukecharming regular";
}
.SubContainerPublicaciones{
    height: auto;
    width: 100%;
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: auto;
    gap: 10px;
    padding: 2% 0%;
}
.ContainerPublicacionesImg1{
    width: auto;
    height: auto;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 0% 0%;
}
.PublicacionesImg{
    width: 100%;
    max-width: 250px;
    height: auto;
}
.PublicacionesImg:hover{
 cursor: pointer;
}
.ContainerPublicacionesTextos1{
    width: 100%;
    height: auto;
    grid-column-start: 2;
    grid-column-end: 3;
    padding: 0% 0%;
}
.ContainerPublicacionesTextos2{
    width: 100%;
    height: auto;
    grid-column-start: 1;
    grid-column-end: 3;
    padding: 0% 0%;
}
.PublicacionesSubTitulo{
    font-size: 1.8vw;
}
.PublicacionesParrafos{
    font-size: 1.8vw;
}
/*----------------------------------------------RESPONSIVE DESING BY FRANCO----------------------------------------*/
@media screen and (max-width: 600px){
    .ContainerPublicacionesImg1{
        grid-column-start: 1;
        grid-column-end: 3;
        grid-row-start: 1;
        grid-row-end: 2;
    }
    .PublicacionesImg{width: 60%;height: auto;}
    .ContainerPublicacionesTextos1{
        grid-column-start: 1;
        grid-column-end: 3;
        grid-row-start: 2;
        grid-row-end: 3;
        padding: 2% 2%;
    }
    .ContainerPublicacionesTextos2{
        grid-column-start: 1;
        grid-column-end: 3;
        grid-row-start: 4;
        grid-row-end: 5;
        padding: 0% 2%;
    }
    .PublicacionesTitulo{font-size: 6vw;}
    .PublicacionesSubTitulo{font-size: 4vw;}
    .PublicacionesParrafos{font-size: 4vw;}
}
