.ContenedorPortadaBlog{
    width: 100%;
    height: auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto;
    position: relative;
}
.ContenedorImgBlog{
    width: 100%;
    height: auto;
}
.ImgBlog{
    width: 100%;
    height: auto;
}
.ContenedorTextos{
    width: 100%;
    height: auto;
    background-color: #07bb9c;
    padding: 20% 10% 2% 10%;
}
.SubContenedorTextos{
    width: 100%;
    height: auto;
}
.Blogtitulo{
    color: white;
    font-family: "varela round regular";
    font-size: 6vw;
}
.Blogtexto{
    color: white;
    font-family: "varela round regular";
    font-size: 2.5vw;
}
/*----------------------------------------------RESPONSIVE DESING BY FRANCO----------------------------------------*/
@media screen and (max-width: 500px){
    .ContenedorPortadaBlog{
   
        width: 100%;
        height: auto;
    
        display: grid;
        grid-template-columns: auto;
        grid-template-rows: auto;
        position: relative;
    }
    .ContenedorImgBlog{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .ImgBlog{
        width: 60%;
        height: auto;
    }
    .ContenedorTextos{
        width: 100%;
        height: auto;
        background-color: #07bb9c;
        padding: 5% 12% 2% 10%;
    }
    .SubContenedorTextos{
        width: 100%;
        height: auto;
    }
    .Blogtitulo{
        color: white;
        font-family: "varela round regular";
        font-size: 12vw;
    }
    .Blogtexto{
        color: white;
        font-family: "varela round regular";
        font-size: 5vw;
    }
}